@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@400;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed, 
figure, figcaption, footer, header, hgroup, 
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
	font-family: 'Roboto';
	color: #94a1b2;
}
/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, 
footer, header, hgroup, menu, nav, section {
	display: block;
}
body {
	line-height: 1;
}
ol, ul {
	list-style: none;
}
blockquote, q {
	quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
	content: none;
}
table {
	border-collapse: collapse;
	border-spacing: 0;
}

html, body, #root, #app {
	height: 100%;
	width: 100%;
	background-color: #242629;
}

main {
	height: calc(100% - 130px);
	padding: 10px 15px;
    margin: 0 auto;
    overflow-y: auto;
    overflow-x: hidden;
}

.loader {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 151px;
    height: 54px;
    font-size: 0;
    margin: 200px auto 0 auto;
}
.loader > .elem {
    display: inline-block;
    vertical-align: middle;
    width: 12px;
    height: 12px;
    background: transparent;
    border-radius: 50px;
    -webkit-animation: anim-loader 2.5s ease-in-out infinite;
            animation: anim-loader 2.5s ease-in-out infinite;
}
.loader > .elem:nth-child(2) {
    margin-left: 12px;
    -webkit-animation-delay: 0.2s;
            animation-delay: 0.2s;
}
.loader > .elem:nth-child(3) {
    margin-left: 12px;
    -webkit-animation-delay: 0.4s;
            animation-delay: 0.4s;
}
.loader > .elem:nth-child(4) {
    margin-left: 12px;
    -webkit-animation-delay: 0.6s;
            animation-delay: 0.6s;
}
.loader > .elem:nth-child(5) {
    margin-left: 12px;
    -webkit-animation-delay: 0.8s;
            animation-delay: 0.8s;
}
.loader > .elem:nth-child(6) {
    margin-left: 12px;
    -webkit-animation-delay: 1s;
            animation-delay: 1s;
}
.loader > .elem:nth-child(7) {
    margin-left: 12px;
    -webkit-animation-delay: 1.2s;
            animation-delay: 1.2s;
}
@-webkit-keyframes anim-loader {
    0% {
        height: 12px;
        box-shadow: none;
   }
    30% {
        height: 70px;
        box-shadow:  -5px 5px 10px #1f2023, 5px -5px 10px #292c2f;
   }
    60% {
        height: 12px;
        box-shadow: none;
   }
}
@keyframes anim-loader {
    0% {
        height: 12px;
        box-shadow: none;
   }
    30% {
        height: 70px;
        box-shadow:  -5px 5px 10px #1f2023, 5px -5px 10px #292c2f;
   }
    60% {
        height: 12px;
        box-shadow: none;
   }
}

::-webkit-scrollbar {
    width: 2px;
    height: 2px;
}
::-webkit-scrollbar-button {
    width: 0px;
    height: 0px;
}
::-webkit-scrollbar-thumb {
    background: #e1e1e1;
    border: 0px none #ffffff;
    border-radius: 50px;
}
::-webkit-scrollbar-thumb:hover {
    background: #ffffff;
}
::-webkit-scrollbar-thumb:active {
    background: #000000;
}
::-webkit-scrollbar-track {
    background: rgba(0,0,0,0);
    border: 0px none #ffffff;
    border-radius: 50px;
}
::-webkit-scrollbar-track:hover {
    background: rgba(0,0,0,0);
}
::-webkit-scrollbar-track:active {
    background: #333333;
}
::-webkit-scrollbar-corner {
    background: transparent;
}


nav {
    height: 70px;
    width: calc(100% - 30px);
    max-width: 500px;
    border-radius: 60px;
    background: linear-gradient(225deg, #202225, #27292c);
    box-shadow:  -5px 5px 10px #1f2023, 5px -5px 10px #292c2f;
    margin: 10px auto 0 auto;
}
nav ul {
    display: flex;
    flex-direction: row;
    width: 100%;
    height: 100%;
}
nav ul a {
    width: calc(100%/3);
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-decoration: none;
    -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}
nav ul li.active, nav ul li.active * {
    color: #7f5af0;
}
nav ul a li {
    display: flex;
    flex-direction: column;
    align-items: center;
    color: #fffffe;
    font-size: 0.8em;
}
nav ul li span svg {
    margin-bottom: 5px;
    height: 20px;
    color: #fffffe;
}
#launches div.launches-list {
    margin: 30px auto 0 auto;
    display: flex;
    flex-direction: column;
}
#launches div.launch-item {
    border-radius: 65px;
    margin-left: auto;
    margin-right: auto;
    height: 100px;
    min-height: -webkit-fit-content;
    min-height: -moz-fit-content;
    min-height: fit-content;
    width: -webkit-fill-available;
    width: -moz-available;
    width: stretch;
    max-width: 900px;
    padding: 15px;
    display: flex;
    flex-direction: row;
    overflow: hidden;
    margin-bottom: 30px;
    background: linear-gradient(225deg, #202225, #27292c);
    box-shadow:  -5px 5px 10px #1f2023, 5px -5px 10px #292c2f;
}
.image-container {
    height: 100%;
    width: 100px;
    border-radius: 50px;
    overflow: hidden;
    background-position: top;
    background-size: cover;
    background-repeat: no-repeat;
}
.launch-info {
    padding-left: 10px;
    width: calc(100% - 110px);
}
.launch-info h2 {
    font-weight: 700;
    color: #FFFFFE;
}
.launch-info p {
    font-size: 0.8em;
    margin-top: 15px;
}
.launch-info .date {
    text-transform: capitalize;
    color: #FFFFFE;
}
.launch-info .date span {
    font-size: 0.8em;
}
.launch-info a {
    color: #7f5af0;
    font-size: 0.8em;
    margin-top: 7px;
    display: block;
    text-decoration: none;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
}

@media only screen and (min-width: 800px) {
    .launch-info a {
        display: inline-block;
        margin-right: 20px;
        margin-top: 15px;
    }
}
.author-credits {
	font-size: 0.7em;
	opacity: 0.5;
	text-align: center;
    margin-top: 10px;
    display: flex;
    flex-direction: column;
}
.author-credits span:first-of-type {
    margin-bottom: 5px;
}

h1 {
    margin-top: 30px;
    font-size: 1.6em;
    display: flex;
    align-items: center;
    justify-content: center;
	font-weight: 700;
	color: #FFFFFE;
}
